<template>
    <el-dialog title="" :visible="issubmitshow" custom-class="submit-dialog" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <div class="const_wrap mb-md">
            <h1 class="mb-md mt-0"></h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{ curdate }}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{ applyID }}</td>
                </tr>
                <tr>
                    <td class="b_gray"></td>
                    <td class="t-l">{{ org_full_name }}</td>
                    <td class="b_gray"></td>
                    <td class="t-l">{{ name }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{ toname }}</td>
                    <td class="b_gray"></td>
                    <td class="t-l">{{ bank_card }}</td>
                </tr>
                <tr>
                    <td class="b_gray"></td>
                    <td colspan="3" class="t-l">{{ bankname }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td ></td>
                    <td width="80"></td>
                    <td width="150"></td>
                    <td width="80"></td>
                    <td width="80"></td>
                    <td width="80"></td>
                </tr>
                <tr v-for="item in tabledata">
                    <td >{{item.asset_typename}}</td>
                    <td width="80">{{ item.asset_brandname }}</td>
                    <td width="150">{{ item.name }}</td>
                    <td width="80">{{ item.price }}</td>
                    <td width="80">{{ item.amount }}</td>
                    <td width="80"> {{ item.total }}</td>
                </tr>
                <tr>
                    <td >&nbsp;</td>
                    <td width="80">&nbsp;</td>
                    <td width="150">&nbsp;</td>
                    <td width="80">&nbsp;</td>
                    <td width="80" class="t-c"></td>
                    <td width="80"> {{ total }}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="15%"></td>
                    <td colspan="5" width="150" class="t-l">{{ des }}</td>
                </tr>
                <tr>
                    <td class="b_gray" width="15%"></td>
                    <td class="t-l" width="20%">{{constperson}}</td>
                    <td class="b_gray" width="15%"></td>
                    <td class="t-l" width="20%">{{copyperson == ''?'--':copyperson}}</td>
                    <td class="b_gray" width="15%"></td>
                    <td class="t-l" width="15%">--</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible"> </el-button>
            <el-button @click="onsubmitform" type="primary" :loading="loading"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
    import Cookies from 'js-cookie';
    import { mapGetters,mapState } from 'vuex';
    export default{
        name:'SubmitModal',
        props:['issubmitshow','loading','toname','bank_card','bankname','tabledata','total','des'],
        data(){
            return{
                hide:false,
            }
        },
        computed: {
            ...mapGetters([
                'org_full_name',
                'name',
                'curdate'
            ]),
            applyID(){
                return this.$store.state.purchase.purchase_addApplyID;
            },
            constperson(){
                let constperson = "";
                if (this.$store.state.purchase.purchase_constdata.length > 0) {
                    constperson = (this.$store.state.purchase.purchase_constdata).map(data => data.split("/")[1].split(" ")[0]).join("");
                }
                return constperson;
            },
            copyperson(){
                let copyperson = "";
                if (this.$store.state.purchase.purchase_copydata.length > 0) {
                    copyperson = (this.$store.state.purchase.purchase_copydata).map(data => data.split("/")[1].split(" ")[0]).join("");
                }
                return copyperson;
            }
        },
        created(){
            this.$store.dispatch('Purchase_AddApplyID',{type:5});
        },
        methods:{
            isVisible(){
                this.$emit('TogglesubmitDialog',{issubmitshow:false});
            },
            onsubmitform(){
                this.$emit('Onsubmit');
            },
        }
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .const_wrap {
        text-align: center;

        h1 {
            font-weight: 500;
        }
        table {
            width: 100%;
            border-collapse: collapse;
            border: 0;

            tr:last-child {

                td {
                    border-bottom: 0;
                }
            }
            td {
                border: 1px solid #E9E9E9;
                padding: 8px;
                line-height: 23px;
                font-size: 12px;

                &
                .b_gray {
                    background: #F2F2F2;
                }
            }
        }
        table:last-child {
            tr:last-child {
                td {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
</style>


