import { Apply_list,Enter_Storage_List} from '../../../api/administration';


const ApplyList = {
    state: {
        Apply_table:[],//
        EnterStorageListData:[],//
    },
    mutations: {
        SET_Apply_table: (state, data) => {
            state.Apply_table = data;
        },
        SET_EnterStorageListData: (state, data) => {
            state.EnterStorageListData = data;
        },
    },
    actions: {
        //
        ApplyList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Apply_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Apply_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        Enter_Storage:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Enter_Storage_List(data).then(response => {
                    let data = response.data.data;
                    commit('SET_EnterStorageListData',data);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
    }
};


export default ApplyList;


