<template>
    <div class="attachment-wrap">
        <div class="const_wrap mb-md">
            <h1 class="mb-md"></h1>
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{date}}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{applyID}}</td>
                </tr>
                <tr>
                    <td class="b_gray"></td>
                    <td class="t-l">{{create_person}}</td>
                    <td class="b_gray"></td>
                    <td class="t-l">{{leave_time}}</td>
                </tr>
                <tr>
                    <td class="b_gray"></td>
                    <td class="t-l">{{org_full_name}}</td>
                    <td class="b_gray"></td>
                    <td class="t-l">{{job}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray" height="100"></td>
                    <td class="t-l" colspan="3">{{des}}</td>
                </tr>
                <tr>
                    <td class="t-c b_gray"></td>
                    <td class="t-l" colspan="3">{{stock_status}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="stock_list" width="100%">
                <el-table-column prop="type" label=""></el-table-column>
                <el-table-column prop="sn" label="" width="150"></el-table-column>
                <el-table-column prop="brand" label="" width="120"></el-table-column>
                <el-table-column prop="name" label="" width="120"></el-table-column>
                <el-table-column prop="price" label="" width="100"></el-table-column>
                <el-table-column prop="status" label="" width="80"></el-table-column>
            </el-table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="100"></td>
                    <td class="t-l">{{finance_status}}</td>
                </tr>
                </tbody>
            </table> 
            <el-table :data="finance_list" width="100%">
                <el-table-column prop="id" label="" width="70"></el-table-column>
                <el-table-column prop="time" label="" width="150"></el-table-column>
                <el-table-column prop="des" label=""></el-table-column>
                <el-table-column prop="name" label="" width="200"></el-table-column>
                <el-table-column prop="status" label="" width="80"></el-table-column>
            </el-table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="120"></td>
                    <td class="t-l">{{account_status}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120">QQ</td>
                    <td class="t-l" width="19%">{{qq}}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l" width="19%">{{email}}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l">{{tel}}</td>
                </tr>
                </tbody>
            </table>
            <table width="100%">            
                <tbody>
                <tr>
                    <td class="t-c b_gray" width="120"></td>
                    <td class="t-l">{{work_status}}</td>
                </tr>
                </tbody>
            </table> 
            <table width="100%">
                <tbody>
                <tr>
                    <td class="b_gray" width="120"></td>
                    <td class="t-l" width="19%">{{constperson}}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l" width="19%">{{copyperson}}</td>
                    <td class="b_gray" width="100"></td>
                    <td class="t-l"></td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    export default{
        name:'ReimburseSheet',
        props:[
            'tableData',
            'tips',
            'date',
            'applyID',
            'org_full_name',
            'create_person',
            'constperson',
            'copyperson',
            'leave_time', 
            'stock_list',
            'finance_list',
            'stock_status',
            'finance_status',
            'account_status',
            'work_status',
            'des',
            'job',
            'qq',
            'email',
            'tel'
        ],
        data(){
            return{

            }
        },
    }
</script>
<style rel="stylesheet/less" lang="less" scoped>
    .attachment-wrap{
        padding:0 20px 25px 20px;
        border-radius: 4px;
        border: 1px solid #E9E9E9;
        .const_wrap {
            text-align: center;

            h1 {
                font-weight: 500;
            }
            table {
                width: 100%;
                border-collapse: collapse;
                border: 0;

                tr:last-child {

                    td {
                        border-bottom: 0;
                    }
                }
                td {
                    border: 1px solid #E9E9E9;
                    padding: 8px;
                    line-height: 23px;
                    font-size: 12px;
                    color: rgba(0,0,0,0.65);

                    &
                    .b_gray {
                        background: #F2F2F2;
                    }
                }
            }
            table:last-child {
                tr:last-child {
                    td {
                        border-bottom: 1px solid #E9E9E9;
                    }
                }
            }
        }
    }
</style>


