import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import router from './router';
import App from './App.vue'
import store from './store'
import NProgress from 'nprogress'; // Progress 
import 'nprogress/nprogress.css'

Vue.use(ElementUI);

// permissiom judge
function hasPermission(roles, permissionRoles) {
    //if (roles.indexOf('126') >= 0) return true; // admin 
    if (!permissionRoles) return true;
    return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

router.beforeEach((to, from, next) => {
    NProgress.start(); // Progress
    if (store.getters.roles.length === 0) { // user_info
        store.dispatch('GetInfo').then(res => { // user_info
            if(res.data.code == 200){
                let roles = [res.data.data.roleName];
                store.dispatch('GenerateRoutes', {roles}).then((response) => { // 
                    router.addRoutes(store.getters.addRouters); // 
                    next({path: to.path , query: to.query }); // hack addRoutes
                })
            }else if(res.data.code == 401){
                location.href=res.data.data.login_url;
            }
        }).catch(err => {
            console.log(err);
        });
    }
    else {
        //next();
        // next()  
        if (hasPermission(store.getters.roles, to.meta.role)) {
            next();
        } else {
            next({path: '/loginout', query: {noGoBack: true}});
        }
        //  
    }
});


router.afterEach(() => {
    NProgress.done(); // Progress
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');



