<template>
    <el-dialog title="" :visible="iscardshow" :before-close="isVisible" :close-on-click-modal="hide" :close-on-press-escape="hide">
        <el-form :model="ruleForm" ref="ruleForm">
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span></el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="bank_name" :rules="[{ required: true, message: ''}]" >
                        <el-select v-model="ruleForm.bank_name" placeholder="" style="width: 100%" >
                            <el-option
                                    v-for="(item,key) in options"
                                    :key="key"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span></el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="card_id" :rules="[{ required: true, message: ''}]" >
                        <el-input v-model="ruleForm.card_id" placeholder="" style="width: 100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col class="w92 t-r t-xs-l mb-sm"><span class="c-red mr4">*</span></el-col>
                <el-col :xs="24" :sm="20">
                    <el-form-item prop="bank_des" :rules="[{ required: true, message: ''}]" >
                        <el-input
                                type="textarea"
                                :rows="2"
                                placeholder=""
                                v-model="ruleForm.bank_des" style="width: 100%">
                        </el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="isVisible('ruleForm')"> </el-button>
            <el-button type="primary" @click="onsubmitform('ruleForm')" :loading="loading"> </el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default{
        props:['iscardshow','carddata'],
        data(){
            return{
                ruleForm:{
                    bank_name:'',
                    card_id:'',
                    bank_des:'',
                },
                hide:false,
                loading:false,
                options:[
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                    {value:'',label:''},
                ],
            }
        },
        computed: {

        },
        methods:{
            isVisible(formName){
                this.$emit('ToggleCardDialog',{iscardshow:false});
                if(typeof(formName) != 'function'){
                    this.$refs[formName].resetFields();
                }
            },
            onsubmitform(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        let carddata = this.ruleForm;
                        this.$store.dispatch('Add_cardsubmit',{carddata:carddata}).then(response=>{
                            if(response.data.code==200){
                                this.$store.dispatch('InitCard');
                            }
                        });
                        setTimeout(()=>{
                            this.$refs[formName].resetFields();
                            this.loading = false;
                            this.$emit('ToggleCardDialog',{iscardshow:false});
                            this.$refs[formName].resetFields();
                        },300);
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>


