import { Inventory_list,Asset_child_list,Scrap_lose,Inventory_details_list,Asset_trajectory,add_sn} from '../../../api/administration';


const InventoryList = {
    state: {
        Inventory_table:[],//
        Asset_child_table:[],//
        assetTrajectory_table:[],//

    },
    mutations: {
        //
        SET_Inventory_table: (state, data) => {
            state.Inventory_table = data;
        },
        //
        SET_Asset_table: (state, data) => {
            state.Asset_child_table = data;
        },
        //
        SET_Trajectory_table: (state, data) => {
            state.assetTrajectory_table = data;
        },
    },
    actions: {
        //
        InventoryList_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Inventory_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Inventory_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        Asset_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Asset_child_list(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Asset_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        ScrapLose_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Scrap_lose(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        InventoryDetails_table:({ commit},data) =>{
            return new Promise((resolve, reject) => {
                Inventory_details_list(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //
        AssetTrajectory_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                Asset_trajectory(data).then(response => {
                    let data = response.data.data;
                    commit('SET_Trajectory_table',data.list);
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },
        //sn
        addSn_table:({ commit },data) =>{
            return new Promise((resolve, reject) => {
                add_sn(data).then(response => {
                    resolve(response);
                }).catch(error => {
                    reject(error);
                });
            });
        },

    }
};


export default InventoryList;


