import fetch from '../utils/fetch';
import qs from 'qs';

export function logout() {
    return fetch({
        url: '/oa_v1/default/login-out',
        method: 'get'
    });
}

export function getInfo() {
    return fetch({
        url: '/oa_v1/default/get-user-info',
        method: 'get'
    });
}

